// Calculates entropy.
export function entropy(length, charpool) {
    return Math.log2(Math.pow(charpool, length));
}

// Calculates the number of combinations given a length and a charset.
export function combinations(length, charpool) {
    return Math.pow(charpool, length);
}

// Returns the password type given a string.
export function pw_type(string){
    // Matchs numbers.
    let numeric = string.match(/[0-9]/g);
    // Match lowercase.
    let lowercase = string.match(/[a-z]/g);
    // Match uppercase.
    let uppercase = string.match(/[A-Z]/g);
    // Match special characters.
    let special = string.match(/[!@#\$%\^\&*\)\(+=._-]/g);

    // Array of regexes, charsets and amounts.
    let regexes = [numeric, lowercase, uppercase, special];
    let charsets = ["Numeric", "Alphabetic Case-Insensitive", "Alphabetic Case-Sensitive" , "Alphanumeric Case-Insensitive", "Alphanumeric Case-Sensitive", "Alphanumeric + Special characters"]
    let amounts = [10, 26, 26, 8];

    // Calculates the length of the charset
    let boolarray = [];
    let total = 0;
    let charset = "";
    for (let i = 0; i < amounts.length; i++){
        if(regexes[i]){
            boolarray.push(true);
            total += amounts[i];
        } else {
            boolarray.push(false);
        }
    }

    // Selects the charset.
    if(special){
        charset = charsets[charsets.length-1];
    } else {
        if(lowercase && uppercase && numeric){ 
            charset = charsets[charsets.length-2];
        } else {
            if(lowercase && numeric || uppercase && numeric){
                charset = charsets[charsets.length-3];
            } else {
                if(lowercase && uppercase){
                charset = charsets[charsets.length-4];
                } else {
                    if(lowercase || uppercase){
                        charset = charsets[charsets.length-5];
                    } else {
                        charset = charsets[charsets.length-6];
                    }
                }
            }
        }
    }
    return {amount:total, charset:charset, types: regexes}
}


export function pw_strength(entropy){
    let strengths = ["Very Weak", "Weak", "Reasonable", "Strong", "Very Strong"]
    if(entropy < 28){
        return {strength:strengths[0], value:0};
    } else if(entropy > 28 && entropy < 35){
        return {strength:strengths[1], value:1};
    } else if(entropy > 35 && entropy < 59){
        return {strength:strengths[2], value:2};
    } else if(entropy > 59){
        return {strength:strengths[3], value:3};
    } else {
        return {strength:strengths[3], value:3};
    }
}

