
// # 1 Random Words 
// # 2 Code Words
// # 3 Random Characters
export function generatePassword(type, length, dict, phrase){
    let str = "";
    switch (type){
      case 1:
        while(str.length < length){
          str += randomWord(dict);
        }
        break;
      case 2:
        for (var i = 0; i < length; i++){
          str += randomCharacter();
        }
        break;
      case 3:
        for (var i = 0; i < length; i++){
          str += passPhrase(dict, phrase.charAt(i));
        }
        break;
      default:
        break;
    }
    return str;
  }
  
  
function randomWord(dict){
    var keys = Object.keys(dict);
    var result = keys[keys.length * Math.random() << 0];
    console.log(result);
    return result;
}
  
function randomCharacter(){
    var chars = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789!@#$%^&*()_+';
    var charLen = chars.length;
    return chars.charAt(Math.floor(Math.random() * charLen));
}

function passPhrase(dict, char){
  char = char.toUpperCase();
  console.log(dict[char])
  if(dict.hasOwnProperty(char)){
    var result = dict[char][Math.floor(Math.random() * dict[char].length)];
  }
  console.log(result);
  return result;
}



  