import React from 'react';
import {PassBar} from './table';
export const Strength = props => (
  <div className="Center">
    <h2 className="strtitle">PASSWORD STRENGTH</h2>
    <div>
      <input id="inputPass" name="incurrent" type="text" placeholder="ENTER PASSWORD" onChange={props.handleChange}></input>
      <PassBar barStyle={props.barStyle} bits={props.bits}></PassBar>
    </div>
    <div className="IssueList">
      {props.children}
    </div>
  </div>
)

export const Issue = props => (
  <div>
    <div className="IssueTop">{props.problem}</div>
    <div className="IssueBottom">{props.fix}</div>
  </div>
)

export function buildStrength(value, handleChangeStrength){
    if(value === undefined){
        let noneStyle = {display: 'none'};
        let obj = {barStyle: noneStyle}
        value = obj;
    } 
    let issues = value.issues;
    var IssueList = null;
    if(issues != undefined && issues.length != []){
        IssueList = issues.map(issue => {
          return (
              <Issue problem={issue.problem} fix={issue.fix}></Issue>
          )
        })
    } 
    
    return <Strength handleChange={handleChangeStrength} barStyle={value.barStyle} bits={value.bits}>
                {IssueList}
            </Strength>
}

