import jslrs from 'js-longest-repeated-substring';

export function issues(string, types, length, dict) {
  let issues = []
  let typeIssue = checkType(types);
  if(typeIssue != null){
    issues.push(typeIssue)
  }
  let lengthIssue = checkLength(length)
  if(lengthIssue != null){
    issues.push(lengthIssue)
  }
  let wordIssue = checkWord(string, dict);
  if(wordIssue != null){
    issues.push(wordIssue);
  }
  let patternIssue = checkPattern(string);
  if(patternIssue != null){
    issues.push(patternIssue);
  }
  return issues;
}

function checkWord(string, dict){
  let words = findWord(string, dict)
  if(words != null){
    return {problem:"Your password contains a dictionary word.", fix:"Obfuscating words by using special characters or not using dictionary words."}
  } else {
    return null;
  }
}

function checkType(types){
  let checkAll = types.every(v => v === types[0]);
  if(checkAll){
      return null;
  }
  let problem = "Your password is missing these character types:";
  let fix = "Add these character types your password:";
  if(types[0] == null){
    problem += " Numeric,";
    fix += " Numeric,"
  }
  if(types[1] == null){
    problem += " Lowercase,";
    fix += " Lowercase,";
  }
  if(types[2] == null){
    problem += " Uppercase,";
    fix += " Uppercase,";
  }
  if(types[3] == null){
    problem += " Special."
    fix += " Special.";
  }
  return {problem: problem, fix: fix};
}

function checkPattern(string){
  let pattern = jslrs.lrs(string);
  if(pattern != null){
    if(pattern.length >= 3){
      return {problem:"Your password has a pattern which will allow for easier password cracking.", fix:"Remove or obfuscate patterns that show up in your password."}
    }
  }
  return null;
}

function checkLength(length){
  if(length >= 10){
    return null; 
  } else if(length >= 20) {
    return{problem:"Your password is less greater than 19 characters.", fix:"This would be very difficult to remember without a passphrase or password manager."}
  } else {
    return{problem:"Your password is less than 9 characters.", fix:"A strong password needs to contain more than 9 characters."}
  }

}

function findWord(letters, dict) {
  // Clone the array for manipulation
  var curLetters = letters.split('');
  //console.log(curLetters)
  let word = "";
  // Make sure the word is at least 3 letters long
  while (curLetters.length > 2) {
      // Get a word out of the existing letters
      word = curLetters.join("").toUpperCase();
      // And see if it's in the dictionary
      
      if (dict[word]) {
          // If it is, return that word
          console.log("FOUND: " + word);
          return word;
      }
      // Otherwise remove another letter from the end
      curLetters.pop();
  }
}
