export function makeDict(txt){
  // Get an array of all the word
  var words = txt.split("\n");
  // And add them as properties to the dictionary lookup
  // This will allow for fast lookups later
  let dict = {};
  let alphadict = {};

  for (var i = 0; i < words.length; i++ ) {
    dict[words[i]] = true;
    let c = words[i][0];
    if(alphadict[c] == undefined){
      alphadict[c] = [words[i]];
    } else {
      alphadict[c] = alphadict[c].add(words[i]);
    }
  }
  return {dict:dict, alphadict:alphadict};
}