import React from 'react';

export const Guideline = props => (
<div className="Center">
    <h2>Guidelines</h2>
    <div>
        <ul>Use a minimum password length of 10 or more characters if permitted.</ul>
        <ul>Include lowercase and uppercase alphabetic characters, numbers and symbols if permitted.</ul>
        <ul>Avoid character repetition, keyboard patterns, dictionary words, letter or number sequences.</ul>
    </div>
</div>
)


export const Problems = props => (
<div className="Center">
    <h1>Problems</h1>
    <div className="Center">
        <ul>Default passwords (as supplied by the system vendor and meant to be changed at installation time): password, default, admin, guest, etc. Lists of default passwords are widely available on the internet.</ul>
        <ul>Dictionary words: chameleon, RedSox, sandbags, bunnyhop!, IntenseCrabtree, etc., including words in non-English dictionaries.</ul>
        <ul>Words with numbers appended: password1, deer2000, john1234, etc., can be easily tested automatically with little lost time.</ul>
        <ul>Words with simple obfuscation: p@ssw0rd, l33th4x0r, g0ldf1sh, etc., can be tested automatically with little additional effort. For example, a domain administrator password compromised in the DigiNotar attack was reportedly Pr0d@dm1n.</ul>
        <ul>Doubled words: crabcrab, stopstop, treetree, passpass, etc.</ul>
        <ul>Common sequences from a keyboard row: qwerty, 123456, asdfgh, fred, etc.</ul>
        <ul>Numeric sequences based on well known numbers such as 911 (9-1-1, 9/11), 314159... (pi), 27182... (e), 112 (1-1-2), etc.</ul>
        <ul>Identifiers: jsmith123, 1/1/1970, 555–1234, one's username, etc.</ul>
        <ul>Weak passwords in non-English languages, such as contraseña (Spanish) and ji32k7au4a83 (bopomofo keyboard encoding from Chinese)</ul>
        <ul>Anything personally related to an individual: license plate number, Social Security number, current or past telephone numbers, student ID, current address, previous addresses, birthday, sports team, relative's or pet's names/nicknames/birthdays/initials, etc., can easily be tested automatically after a simple investigation of a person's details.</ul>
        <ul>Dates: dates follow a pattern and make your password weak.</ul>
    </div>
</div>
)


export function expo(x, f) {
  return Number.parseFloat(x).toExponential(f);
}

export function financial(x) {
  return Number.parseFloat(x).toFixed(2);
}

// Check if string is valid.
export function checkString(string){
  if(string == null) {
    console.log("String is null.")
  } else if(string == "") {
    console.log("String is empty.")
  } else {
    return true;
  }
}

